#about_container{
    background-position: center;
    display: flex;
    justify-content: space-between;
    align-content: flex-end;
    align-items: flex-end;
    min-height: 100vh;
    width:100vw;
    padding: 50px;
    gap:50px;
}
#inner_container{
    color: white;
    border-radius: 10px;
}
/* #right_container{
    align-self: flex-start;
} */
#hi{
    font-size: 4rem;
}
#name{
    color:greenyellow;
    stroke: red;
    stroke-width: 3px;
    font-size: 5rem;
}
#name span{
    padding: 5px  10px;
    border-radius: 3px;
}
#residing_at{
    margin:15px 0px;
}
#interest_container{
    display: flex;
    grid-gap:10px;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin-top: 10px;
    align-items: center;
}
#interest_tag{
    font-size:24px;
}
.interests{
    background-color: green;
    color: white;
    border-radius:50px;
    padding:5px 20px;
    display: inline-block;
    font-weight:bold;
    /* border: 1px solid gray; */
    /* box-shadow: inset 0px 0px 0px 1px rgba(255, 255, 255, 1); */
}
.heading{
    color:grey;
    font-weight: normal;
}
.icons_container{
    display: flex;
    flex-wrap: wrap;
    max-width: max-content;
    gap:20px;
    padding:15px 20px;
    border-radius: 10px;
    background-color: white;
}
.icon{
    height:35px;
    vertical-align: middle;
    transition: all 0.3s;
}
.icon:hover{
    height:35px;
    transform: scale(1.2);
}
@media screen and (max-width:700px) {
    #about_container{
        display: grid;
        align-content: space-around;
        padding: 40px;
        /* gap:50px; */
    }
    #hi{
        font-size: 1.5rem;
    }
    #name{
        font-size: 1.5rem;
        margin:20px 0px;
    }
}