#skills_heading{
    color: white;
    padding: 50px;
    background-color: rgba(240, 248, 255, 0.2);
    width: auto;
    font-size: 3rem;   
}
#skills_container{
    display: flex;
    gap:15px;
    padding:30px;
    flex-wrap: wrap;
    justify-content: center;
}
.skill_cointainer{
    display: flex;
    justify-content: stretch;
    justify-items: center;
    align-items: stretch ;
    height : 120px;
    width: 300px;
}
.icon_container{
    background-color: white;
    flex:0 0 0;
    width:120px;
    height:120px;
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    padding: 20px;
    /* border-right: 2px solid grey; */
}
.skill_icon{
    height: auto;
    width: 70px;
}
.skill_cointainer .details_container{
    padding:10px 15px;
    background-color: rgb(223, 255, 174);
}
.name{
    color: crimson;
    margin-bottom: 8px;
    font-size: 1.5rem;
}
.important_concepts{
    display: flex;
    flex-wrap: wrap;
    gap:5px;
}
.concept{
    background-color: steelblue;
    color: white;
    border-radius: 3px;
    padding: 5px 15px;
    display: inline;
    font-size: smaller;
}