.inner_container{
    /* display: flex; */
    gap:30px;
    /* padding: 30px; */
    border-radius: 2px;
    background-color: white;
    justify-items: stretch;
    max-width: 500px;
}
.details_container{
    padding:30px;
    background-color: rgb(219, 255, 251);
}
.project_name{
    font-size: xx-large;
    font-family: serif;
    background-color: white;
    color:crimson;
    padding: 30px;
}
.ribbon{
    display: inline-block;
    background-color: navy;
    color:white;
    padding:5px 15px;
    width: 100%;
    text-align: center;
}
.project_link{
    text-decoration: none;
    background-color: dodgerblue;
    color: white;
    padding: 5px 15px;
    margin-bottom: 10px;
    border-radius: 3px;
}
.duration{
    margin:20px 0px;
}
.skills_container{
    display: flex;
    flex-wrap: wrap;
    gap:10px;
    margin: 20px 0px;
}
.skill{
    background-color: orangered;
    color:white;
    padding:5px 20px;
    border-radius: 3px;
    font-weight: bold;
}
.description{
    list-style: disc;
    list-style-position: inside;
}
.description li{
    margin: 10px 0px;
    color: rgb(70, 70, 70);
    font-style: italic;
}
.image_or_iframe_container img{
    height: 350px;
    vertical-align: middle;
    border-radius: 2px;
    filter:saturation(200%);
}
@media screen and (max-width:1100px) {
    .inner_container{
        flex-wrap: wrap;
    }
    .image_or_iframe_container img{
        height: auto;
        width: 100%;
        vertical-align: middle;
        border-radius: 2px;
    }
}
